<template>
  <div class="body" v-bind="res">
    <div class="contents">
      <div class="img_box">
        <!-- {{ this.$route.params }} -->
        <img :src="goRes.car_img" :alt="goRes.car_name" />
      </div>
      <div class="contents_box">
        <dl class="c_box_01">
          <dt class="rank_name">{{ goRes.car_name }}</dt>
          <dd class="fuel_name">{{ goRes.car_fuel }}</dd>
          <dd>/  </dd>
        </dl>
        <div class="c_box_02">
          <span class="car_rank">{{ goRes.car_rank }}</span>
          <dd class="star">{{ goRes.car_star }}</dd>
        </div>
        <div class="price c_box_03" :v-model="res.price">
          {{ goRes.car_price }}
        </div>
        <div class="c_box_04">
          <datepicker
            v-model="res.start"
            class="datetime"
            format="yyyy-MM-dd"
            :disabled-dates="disabledStartDates"
          ></datepicker
          ><span></span>
          <datepicker
            v-model="res.end"
            class="datetime"
            format="yyyy-MM-dd"
            :disabled-dates="disabledEndDates"
          ></datepicker
          ><span></span>
        </div>
      </div>
    </div>
    <div class="reservation">
      <div class="reservation_box">
        <b-form-group>
          <label for="clientName"> </label>
          <b-form-input
            ref="clientName"
            v-model="res.clientName"
            name="clientName"
            aria-describedby="  "
            :state="clientNameState"
            required
            trim
            size="sm"
          ></b-form-input>

          <!--    -->
          <b-form-invalid-feedback v-if="res.clientName" id="input-live-feedback">
              2   
          </b-form-invalid-feedback>

          <!--    -->
          <b-form-text v-if="res.clientName" id="input-live-help"></b-form-text>
        </b-form-group>

        <b-form-group>
          <label for="driverName"> </label>
          <b-form-input
            ref="driverName"
            v-model="res.driverName"
            name="driverName"
            aria-describedby=" "
            :state="driverNameState"
            required
            trim
            size="sm"
          ></b-form-input>

          <!--    -->
          <b-form-invalid-feedback v-if="res.driverName" id="input-live-feedback">
              2   
          </b-form-invalid-feedback>

          <!--    -->
          <b-form-text v-if="res.driverName" id="input-live-help"></b-form-text>
        </b-form-group>

        <b-form-group>
          <label for="driverBirth"> </label>
          <b-form-input
            ref="driverBirth"
            v-model="res.driverBirth"
            name="driverBirth"
            type="date"
            size="sm"
            required
            trim
          ></b-form-input>
          <label for="phoneNumber"> </label>
          <b-form-input
            ref="phoneNumber"
            v-model="res.phoneNumber"
            name="phoneNumber"
            required
            trim
            placeholder="000-0000-0000"
            type="text"
            size="sm"
            aria-describedby=" "
            @keyup="getPhoneMask(res.phoneNumber)"
          ></b-form-input>
        </b-form-group>
      </div>
      <b-button @click="dateCheck()"></b-button>
      <div class="payment">
        <h3> </h3>
        <p><span></span>**** *** **** **</p>
        <p><span></span>**** *** **** **</p>
        <p><span></span>**** *** **** **</p>
      </div>
      <div class="pay">
        <button @click="goResCheck(res)">
          <span class="price">{{ goRes.car_price }}</span
          >  
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import datepicker from 'vuejs-datepicker'
export default {
  name: 'GoRes',
  components: { datepicker },
  data() {
    return {
      //    
      disabledStartDates: {
        to: new Date() //   
        // ranges: [
        //   {
        //     //   
        //     from: new Date(2016, 11, 25),
        //     to: new Date(2016, 11, 30)
        //   }
        // ]
      },
      disabledEndDates: {
        to: new Date() //   
        // ranges: [
        //   {
        //     //   
        //     from: new Date(2016, 11, 25),
        //     to: new Date(2016, 11, 30)
        //   }
        // ]
      },
      goRes: this.$route.params, //    
      res: {
        start: '',
        end: '',
        clientName: '',
        driverName: '',
        phoneNumber: '',
        driverBirth: '',
        name: this.$route.params.name,
        img: this.$route.params.img,
        fuel: this.$route.params.fuel,
        rank: this.$route.params.rank,
        star: this.$route.params.star,
        price: this.$route.params.price
      }
    }
  },
  computed: {
    //   
    clientNameState() {
      return this.res.clientName.length > 1 && /^[-]*$/.test(this.res.clientName) //  2 
    },
    driverNameState() {
      return this.res.driverName.length > 1 && /^[-]*$/.test(this.res.driverName) //  2 
    }
  },
  methods: {
    dateCheck() {
      //    ( yyyy-mm-dd)
      console.log(
        `${this.res.start.toLocaleDateString().replace(/\./g, 't').split('t')[0]}, ${
          this.res.start.toLocaleDateString().replace(/\./g, 't').split('t')[1]
        }, ${this.res.start.toLocaleDateString().replace(/\./g, 't').split('t')[2]}`,
        `${this.res.end.toLocaleDateString().replace(/\./g, 't').split('t')[0]}, ${
          this.res.end.toLocaleDateString().replace(/\./g, 't').split('t')[1]
        }, ${this.res.end.toLocaleDateString().replace(/\./g, 't').split('t')[2]}`
      )
    },
    goResCheck(props) {
      // console.log(props)
      if (this.checkInput() === false) {
        // / 
        return false
      } else {
        this.$router.push({
          name: 'goResCheck',
          params: props
        })
      }
    },

    //     
    checkInput() {
      const inputForm = this.res
      // console.log(inputForm)
      if (inputForm.clientName == '') {
        alert('   ')
        this.$refs.clientName.focus()
        return false
      } else if (this.clientNameState === false) {
        this.$refs.clientName.focus()
        return false
      }

      if (inputForm.driverName == '') {
        alert('   ')
        this.$refs.driverName.focus()
        return false
      } else if (this.driverNameState === false) {
        this.$refs.driverName.focus()
        return false
      }

      if (inputForm.phoneNumber == '') {
        alert('  ')
        this.$refs.phoneNumber.focus()
        return false
      } else if (this.formatNumber === false) {
        alert('  ')
        this.$refs.phoneNumber.focus()
        return false
      }

      if (inputForm.driverBirth == '') {
        alert('  ')
        this.$refs.driverBirth.focus()
        return false
      } else {
        return true
      }
    },

    //   
    formatNumber(e) {
      return String(e).substring(0, 13) //  11 010-1234-5678
    },

    //     (-)  
    getPhoneMask(val) {
      let res = this.getMask(val)
      this.res.phoneNumber = res

      // //    '-'   
      // this.$store.getters.User.userPhoneNumber = this.user.userPhoneNumber.replace(/[^0-9]/g, '')
    },

    getMask(inputNumber) {
      if (!inputNumber) return inputNumber
      inputNumber = inputNumber.replace(/[^0-9]/g, '')

      let res = ''
      if (inputNumber.length < 8) {
        res = inputNumber
      } else if (inputNumber.length == 8) {
        res = inputNumber.substr(0, 4) + '-' + inputNumber.substr(4)
      } else if (inputNumber.length == 9) {
        res = inputNumber.substr(0, 3) + '-' + inputNumber.substr(3, 3) + '-' + inputNumber.substr(6)
      } else if (inputNumber.length == 10) {
        res = inputNumber.substr(0, 3) + '-' + inputNumber.substr(3, 3) + '-' + inputNumber.substr(6)
      } else if (inputNumber.length > 10) {
        res = inputNumber.substr(0, 3) + '-' + inputNumber.substr(3, 4) + '-' + inputNumber.substr(7)
      }
      //   }
      // }
      return res
    }
  }
}
</script>

<style lang="scss" scoped>
