          top: -20px;
        }
      }
      .c_box_03 {
        // position: relative;
        // order: 3;
        // width: 40%;
        // text-align: center;
        // align-self: flex-start;
        // font-size: 2em;
        // margin-top: 25px;
        // color: $main;
        display: none;
      }
      .c_box_04 {
        width: 100%;
